<template>
    <div class="songItem">
        <div><slot></slot></div>
        <div class="left">
            <!-- 歌名 -->
            <h3 v-html="$options.filters.highLight(item.name,keywords)"></h3>
            <!-- 歌手 -->
            <p>
                <i></i>
                <template v-if="item.song">
                {{item.song.artists[0].name}}
                </template>
                <template v-if="item.artists">
                {{item.artists[0].name}}
                </template>
                 - {{item.name}}
            </p>
        </div>
        <div class="playbtn" @click="play(item)">
            <span class="playbtn1" v-if="$root.musicStore && $root.musicStore.musicID === item.id"></span>   
            <span class="playbtn2" v-else></span>
        </div>
    </div>
</template>

<script>
export default {
    name:"SongItem",
    props:['item','keywords','tSQ'],
    methods:{
        play(item){
            //this.$emit('play',item.id);//组件传值的方式 触发父组件的play 并传递音乐ID
            //另一种存值方式,存在vue实例里面
            this.$root.musicStore.musicID=item.id;
            this.$root.musicStore.currentMusic=item;
            // this.$root.musicStore.musicList.unshift(item);//把当前歌曲放在播放列表开头
            let newArr = this.$root.musicStore.musicList;
            let find = false;
            for(let n of newArr){ 
                if(n.id==item.id){
                    find=true;
                }
            }
            if(!find) this.$root.musicStore.musicList.push(item)//如果当前音乐在播放列表里面不存在的时候加入播放列表
            //把当前歌曲播放列表加入歌单
            this.$emit('add-list')
        }
    },
    filters:{
        //调用$options.filters.highLight()
        //val 里面如果有keywords值 替换成html
        highLight(val,keywords){
            //判断有没有传递关键字keywords 如果没有就不做正则替换
            if(typeof keywords=="undefined"){
                return val;
            }
            let reg=new RegExp(keywords,'g');//根据关键机子创建一个正则队形
            if(reg.test(val)){
                //把val里面的关键字keywords替换成span+keywords的方式
                return val.replace(reg,'<span class="hightLight">'+keywords+'</span>')
            }
            return val;//如果没有在val里面找到关键字 就直接返回val
        }
    }
}
</script>

<style lang="less">
.songItem{
    display:flex;
    align-items: center;
    border-bottom:1px splid #eee;
    padding: 6px 0px;
    .left{
        flex:1;
        h3{
            color: #333;
        }
        p{
            color:#aaa;
            font-size: 10px;
            i{
                display: inline-block;
                width: 12px;
                height: 8px;
                margin-right: 4px;
                background-image: url(../assets/imgs/index_icon_2x.png);
                background-size: 166px 97px;
            }
        }
        
        .hightLight{
            color: rgb(35, 160, 209);
        }
    }
    .playbtn{
        width: 27px;
        height: 22px;
        
    }
    .playbtn1{
        display: block;
        width: 100%;
        height: 100%;
        background: url(../assets/imgs/musicgl.gif);
        
    }
    .playbtn2{
        display: block;
        width: 100%;
        height: 100%;
        background: url(../assets/imgs/index_icon_2x.png) no-repeat -24px 0px ;
        background-size:166px 97px;
    }
}
</style>
